body {
  background-color: #d2d3db;
  margin: 0%;
  padding: 0%;
}

header {
  text-align: center;
  height: auto;
  padding: 0;
}

header h1 {
  color: #3b423cf1;
  text-align: baseline;
  margin: 0;
  padding: 0%;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 15vh;
}

.dice-container {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 4%;
  margin: 6.5% 0;
  display: flex;
}

.dice {
  perspective: 1000px;
  width: 10rem;
  height: 10rem;
}

.cube {
  width: 10rem;
  height: 10rem;
  transform-style: preserve-3d;
  transition: transform 1s ease-in-out;
  position: relative;
  transform: rotateX(0)rotateY(0);
}

.cube div {
  background-position: center;
  background-size: cover;
  border: 1px solid #000;
  width: 10rem;
  height: 10rem;
  position: absolute;
}

.front {
  transform: rotateY(0)translateZ(5rem);
}

.back {
  transform: rotateY(180deg)translateZ(5rem);
}

.right {
  transform: rotateY(90deg)translateZ(5rem);
}

.left {
  transform: rotateY(-90deg)translateZ(5rem);
}

.top {
  transform: rotateX(90deg)translateZ(5rem);
}

.bottom {
  transform: rotateX(-90deg)translateZ(5rem);
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

.ask-num {
  text-align: center;
  margin: 3%;
}

input[type="number"] {
  text-align: center;
  color: #3b423cf1;
  background-color: #7fffd4;
  width: 4vw;
  height: 3vh;
  padding: 5px;
  font-size: 4vh;
}

label [for="diceCount"] {
  color: #3b423cf1;
  font-size: 4vh;
}

.btn {
  text-align: center;
  justify-content: center;
  margin-top: 10%;
}
