body {
  
  background-color: #d2d3db;
  margin: 0%;
  padding: 0%;
}
header {
  text-align: center;
  padding:0px 0px;
  height: auto;

}
header h1 {
  color:#3b423cf1;
  margin:0px;
  padding: 0%;
  text-align:baseline; 
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 15vh
}

.dice-container {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; /* Allow wrapping on smaller screens */
  gap: 4%; /* Reduced gap for smaller screens */
  margin: 6.5% 0;

}

.dice {
  width: 10rem;
  height: 10rem;
  perspective: 1000px;
}

.cube {
  width: 10rem;
  height: 10rem;
  position: relative;
  transform-style: preserve-3d;
  transform: rotateX(0deg) rotateY(0deg);
  transition: transform 1s ease-in-out;
}

.cube div {
  position: absolute;
  width: 10rem;
  height: 10rem;
  background-size: cover;
  background-position: center;
  border: 1px solid black;
}

.front { transform: rotateY(0deg) translateZ(5rem); }
.back { transform: rotateY(180deg) translateZ(5rem); }
.right { transform: rotateY(90deg) translateZ(5rem); }
.left { transform: rotateY(-90deg) translateZ(5rem); }
.top { transform: rotateX(90deg) translateZ(5rem); }
.bottom { transform: rotateX(-90deg) translateZ(5rem); }

button {
  
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #0056b3;
}
.ask-num{
  text-align: center;
  margin:3% ;
}
input[type="number"] {
  width: 4vw;
  padding: 5px;
  font-size: 4vh;
  text-align: center;
  color:#3b423cf1;
  background-color: aquamarine;
  height: 3vh;

}
label [for="diceCount"]{
  
  font-size: 4vh;
  color:#3b423cf1;
}
.btn{
  margin-top: 10%;
  text-align: center;
  justify-content: center;
}

